import style from "../../UiTab.module.css";
const TileLeaveCard = ({
  tile,
  onTile,
  activeTile,
  customTileChild,
  isLeaveCreate,
  children,
}) => {
  return (
    <div
      className={
        tile?.key === activeTile ? style.tile_tile_active : style.tile_tile
      }
      onClick={() => (isLeaveCreate ? onTile(tile) : onTile(tile?.key))}
      style={customTileChild}
    >
      {children}
    </div>
  );
};

export default TileLeaveCard;
