import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useFileUpload } from "../../../../hooks/authentication-api";
import { leaveTypeList } from "../../../Constant/Lms/lms_constant";
import ChooseLeaveDates from "../../../../MembersTab/staff/Mainbody/Leaves/ChooseLeaveDates";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import {
  useLmsStaffCOffRequestLeave,
  useLmsStaffRequestLeave,
} from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import FormTextarea from "../../../Ui/Input/FormTextarea";
import CreateButton from "../../../Ui/Button/CreateButton";
import FormFile from "../../../Ui/Input/FormFile";
import TileLeaveCard from "../../../Ui/Tab/Tile/TileLeaveCard";
import TileWrapper from "../../../Ui/Tab/Tile/TileWrapper";
import Modal from "../../../Ui/Modal/Modal";
import QvipleLoader from "../../../Loader/QvipleLoader";
import style from "../../FinanceManager/Income/Income.module.css";
const StaffNewLeaveRequest = ({
  leaveConfig,
  sid,
  createdBy,
  onRefetch,
  onClose,
  leaveTypeToggle,
}) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    reason: "",
    dates: [],
    attach: "",
    leave_type: "Casual Leave",
  });
  const [leaveType, setLeaveType] = useState(leaveTypeList);
  const [disabled, setDisabled] = useState(false);
  const [errorField, setErrorField] = useState("");
  const [file, setFile] = useState("");
  const [currentTileActive, setCurrentTileActive] = useState("");
  const [selectDate, setSelectDate] = useState(false);

  const [lmsStaffRequestLeave] = useLmsStaffRequestLeave();
  const [lmsStaffCOffRequestLeave] = useLmsStaffCOffRequestLeave();
  const [fileUpload] = useFileUpload();

  useEffect(() => {
    if (createdBy === "Coff") {
      setState((prev) => ({
        ...prev,
        leave_type: "Compensation Off Leave",
      }));
    } else {
      let lm = leaveTypeList;
      lm[1].count = leaveConfig?.casual_leave;
      lm[2].count = leaveConfig?.medical_leave;
      lm[3].count = leaveConfig?.sick_leave;
      lm[4].count = leaveConfig?.c_off_leave;
      lm[7].count = leaveConfig?.commuted_leave;
      lm[8].count = leaveConfig?.maternity_leave;
      lm[9].count = leaveConfig?.paternity_leave;
      lm[10].count = leaveConfig?.quarantine_leave;
      lm[11].count = leaveConfig?.half_pay_leave;
      lm[12].count = leaveConfig?.study_leave;
      lm[13].count = leaveConfig?.sabbatical_leave;
      lm[14].count = leaveConfig?.special_disability_leave;
      lm[15].count = leaveConfig?.winter_vacation_leave;
      lm[16].count = leaveConfig?.summer_vacation_leave;
      lm[17].count = leaveConfig?.child_adoption_leave;
      lm[18].count = leaveConfig?.bereavement_leave;
      // lm[19].count = leaveConfig?.earned_leave;
      setLeaveType(lm);
      setCurrentTileActive(lm[1]);
    }
  }, [leaveConfig, createdBy]);

  const onSelectType = (val) => {
    setState((prev) => ({
      ...prev,
      leave_type: val?.key,
    }));
    setCurrentTileActive(val);
  };

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onToggleDate = () => {
    setSelectDate((pre) => !pre);
  };
  const onGrabDates = (val) => {
    setState((prev) => ({
      ...prev,
      dates: val,
    }));
  };

  const onFileUpload = (e) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setFile({
            name: e.target.files[0]?.name,
            key: res.data?.imageKey,
            type: e.target.files[0]?.type,
          });
          setState((prev) => ({
            ...prev,
            attach: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onSave = () => {
    if (sid) {
      let validation = requiredField(state, ["attach"]);
      if (existingRequiredField(validation)) {
        setDisabled((pre) => !pre);
        if (createdBy === "Coff") {
          lmsStaffCOffRequestLeave({
            sid: sid,
            leaveConfig: state,
          })
            .then(() => {
              onRefetch();
              setDisabled((pre) => !pre);
              onClose();
            })
            .catch({});
        } else {
          lmsStaffRequestLeave({
            sid: sid,
            LeaveData: state,
          })
            .then(() => {
              onRefetch();
              setDisabled((pre) => !pre);
              onClose();
            })
            .catch({});
        }
      } else {
        setErrorField(validation);
      }
    }
  };

  // console.info("leaveTypeToggle", leaveTypeToggle);
  return (
    <>
      {selectDate ? (
        <ChooseLeaveDates
          onClose={onToggleDate}
          alradyDates={state.dates}
          onGrabDates={onGrabDates}
          currentTileActive={currentTileActive}
          createdBy={createdBy}
        />
      ) : (
        <Modal onClose={onClose}>
          <div
            className={style.modal_container}
            style={{
              maxWidth: createdBy === "Coff" ? "35rem" : "40rem",
            }}
          >
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>
                    {" "}
                    {t(
                      createdBy === "Coff"
                        ? "request_c_off_leave"
                        : "create_new_leave"
                    )}
                  </h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              {createdBy === "Coff" ? null : (
                <TileWrapper
                  customTileContainer={{
                    flexWrap: "wrap",
                    marginBottom: "1rem",
                  }}
                >
                  {leaveType?.map((tile) =>
                    tile.key === "All Leave" ||
                    !leaveTypeToggle[tile?.isActive] ? null : (
                      <TileLeaveCard
                        tile={tile}
                        onTile={onSelectType}
                        customTileChild={
                          ["On Duty Leave", "Leave Without Pay"]?.includes(
                            tile.key
                          )
                            ? {
                                flexDirection: "row",
                                alignItems: "center",
                              }
                            : {
                                flexDirection: "column",
                                alignItems: "flex-start",
                              }
                        }
                        activeTile={currentTileActive?.key}
                        isLeaveCreate
                      >
                        <h6>{t(tile?.name)}</h6>
                        {tile?.count >= 0 &&
                        !["On Duty Leave", "Leave Without Pay"]?.includes(
                          tile.key
                        ) ? (
                          <p>
                            {t("available")} {": "} {tile?.count}{" "}
                          </p>
                        ) : null}
                      </TileLeaveCard>
                    )
                  )}
                </TileWrapper>
              )}

              <FormTextarea
                label={t("reason")}
                placeholder={t("leave_reason_placeholder")}
                name={"reason"}
                value={state.reason}
                type={"text"}
                onChange={onInputChange}
                errorShow={errorField["reason"]}
              />

              <CreateButton
                label={t("select_dates")}
                onAction={onToggleDate}
                customStyle={{
                  width: "10rem",
                  margin: "0",
                  marginTop: "1rem",
                  padding: "0.4rem 1rem",
                }}
              />
              <p className={style.date_select}>
                {state.dates?.map((dt) => (
                  <span key={dt}>
                    {dt}
                    {", "}
                  </span>
                ))}
              </p>
              <FormFile
                label={t("leave_attachment")}
                name={"leave_attachment"}
                value={file}
                onChange={onFileUpload}
              />
              <CreateButton label="create" onAction={onSave} />
            </UserMemberContentWrapper>
          </div>
        </Modal>
      )}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default StaffNewLeaveRequest;
